import request from '@/utils/request'

export const submitOrder = (mode, params) => {
  return request.post('/checkout/submit', {
    mode,
    delivery: 10,
    couponId: 0,
    isUsePoints: 0,
    payType: 10,
    ...params
  })
}

// 订单结算
export const checkOrder = (mode, obj) => {
  return request.get('/checkout/order', {
    params: {
      mode,
      delivery: 0,
      couponId: 0,
      isUsePoints: 0,
      ...obj
    }
  })
}

export const getMyOrderList = (dataType, page) => {
  return request.get('/order/list', { params: { dataType, page } })
}

// 取消订单
export const cancelOreder = (orderId) => {
  return request.post('/order/cancel', { orderId })
}
